import React from 'react';

const Featured = ({match}) => {
var name = match.params.name;
var sub = match.params.sub;
console.log(match);
  return (
    <div className="main-content">
      <h2>{name} </h2>
      <p>Introducing <strong>{name}</strong>, a teacher who loves teaching courses about <strong>{sub}</strong>!</p>
    </div>
  );
}

export default Featured;



